import "./style/global.css";
import menu from "./menu";
import { populateMenu } from "./utils/helper";

const starters = document.getElementById("starters");
const pasta = document.getElementById("pasta");
const pizza = document.getElementById("pizza");
const menuItems = menu.items.sort((a, b) => a.menuOrder - b.menuOrder);
console.log(menuItems);

menuItems.forEach((item) => {
  console.log(item.spicy);
  populateMenu(
    "starters",
    starters,
    item.name,
    item.description,
    item.price,
    item.menuOrder,
    item.type,
    item.spicy
  );
  populateMenu(
    "pasta",
    pasta,
    item.name,
    item.description,
    item.price,
    item.menuOrder,
    item.type,
    item.spicy
  );
  populateMenu(
    "pizza",
    pizza,
    item.name,
    item.description,
    item.price,
    item.menuOrder,
    item.type,
    item.spicy
  );
});

const sliderImages = document.querySelectorAll(".hero__slide");
const arrowLeft = document.querySelector("#arrow-left");
const arrowRight = document.querySelector("#arrow-right");
let current = 0;

// Clear all images
function reset() {
  for (let i = 0; i < sliderImages.length; i++) {
    sliderImages[i].style.display = "none";
  }
}

// Init slider
function startSlide() {
  reset();
  sliderImages[0].style.display = "flex";
}

// Show prev
function slideLeft() {
  reset();
  sliderImages[current - 1].style.display = "flex";
  current--;
}

// Show next
function slideRight() {
  reset();
  sliderImages[current + 1].style.display = "flex";
  current++;
}

// Left arrow click
arrowLeft.addEventListener("click", function () {
  if (current === 0) {
    current = sliderImages.length;
  }
  slideLeft();
});

// Right arrow click
arrowRight.addEventListener("click", function () {
  if (current === sliderImages.length - 1) {
    current = -1;
  }
  slideRight();
});

startSlide();
